@import './variables.scss';

@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url(./fonts/material-design-icons/MaterialIcons-Regular.ttf) format('truetype');
}

@font-face {
  font-family: 'Material Icons Outlined';
  font-style: normal;
  font-weight: 400;
  src: url(./fonts/material-design-icons/MaterialIconsOutlined-Regular.otf) format('opentype');
}

.material-icons,
.material-icons-outlined {
  font-weight: normal;
  font-style: normal;
  font-size: 20px; /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
}
.material-icons {
  font-family: 'Material Icons';
}
.material-icons-outlined {
  font-family: 'Material Icons Outlined';
}

@font-face {
  font-family: 'Roboto';
  src: url('./Roboto/Roboto-Regular.ttf') format('truetype');
  font-weight: normal bold;
  font-weight: 400 500;
}

@font-face {
  font-family: 'Roboto 500';
  src: url('./Roboto/Roboto-Medium.ttf') format('truetype');
  font-weight: normal bold;
  font-weight: 400 500;
}
@font-face {
  font-family: 'Roboto Bold';
  src: url('./Roboto/Roboto-Bold.ttf') format('truetype');
  font-weight: normal bold;
  font-weight: 400 500;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.nodisplay {
  display: none;
}

.app {
  input[type='button'] {
    font-family: 'Roboto', sans-serif;
  }
  .__react_component_tooltip.place-top::after {
    border-top: 8px solid var(--color-white);
  }
  .__react_component_tooltip.place-bottom::after {
    border-bottom: 8px solid var(--color-white);
  }
  .__react_component_tooltip.place-left::after {
    border-left: 8px solid var(--color-white);
  }
  .__react_component_tooltip.place-right::after {
    border-right: 8px solid var(--color-white);
  }
  .react-tooltip {
    background-color: var(--color-white);
    opacity: 1 !important;
    box-shadow: 0 2px 6px var(--DARK-GRAY);
    padding: 10px;
    cursor: auto;
  }
  .tooltip-text {
    font-size: 14px;
    font-family: 'Roboto 500', sans-serif;
    color: var(--PRIMARY-TEXT);
  }
  button {
    border: none;
    background-color: inherit;
    font-family: 'Roboto 500', sans-serif;
    color: var(--SECONDARY-TEXT);
    cursor: pointer;
    font-size: 14px;
    padding: 5px 10px;
    &:hover {
      color: var(--FONT-COLOR);
    }
  }

  .content {
    background-image: none;
    height: calc(100% - var(--navigation-bar-height));
    display: flex;
    position: absolute;
    width: 100%;
    overflow: hidden;

    // Overwrite operations menu resizer 'col-resize' cursor with 'default' cursor.
    .Resizer.vertical.top-level-collapse-pane {
      cursor: default;
    }
    .full-screen-button-container {
      z-index: 5;
      position: absolute;
      display: flex;
      flex-direction: column;
      -webkit-box-align: center;
      align-items: center;
      -webkit-box-pack: center;
      justify-content: center;
      height: 0.5rem;
      right: 0px;
      left: 0px;
      top: 0px;
      bottom: 0px;
    }
    .full-screen-button {
      position: absolute;
      width: 1.8rem;
      height: 1.8rem;
      border-radius: 300px;
      background: #0e6ff9;
      cursor: pointer;
      color: white;
      border: 2px rgba(200, 200, 200, 0.1) solid;
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;

      z-index: 3;
      visibility: hidden;
      opacity: 0;
      transition: all 220ms;
    }
    .full-screen-button-container:hover > .full-screen-button {
      visibility: visible;
      opacity: 1;
      transition: all 250ms;
    }

    // add transition to operations menu during expanding.
    .SplitPane.vertical.top-level-collapse-pane
      > .Pane.vertical.top-level-collapse-pane:first-child {
      transition: flex-basis 350ms, min-width 350ms, flex-grow 350ms;
    }

    .operations-menu {
      background-color: var(--color-white);
      font-family: 'Roboto', sans-serif;
    }

    .non-operations-menu {
      background-color: var(--INTERACTIVE-BACKGROUND);
      display: flex;
      flex: 1;
      flex-direction: column;
      overflow-y: scroll;
      overflow-x: hidden;

      .code-panel-and-events-panel {
        display: flex;
        background-color: var(--INTERACTIVE-BACKGROUND);

        .code-panel {
          height: 100%;

          .cm-scroller {
            overflow: auto;
          }
        }

        // Code panel class when events panel has been minimized
        .code-panel-no-events {
          .cm-scroller {
            overflow: auto;
          }
        }

        .events-panel {
          flex: 1;
          display: flex;
          position: relative;
          height: 100%;
        }
      }

      // Code and Events panel class when operations menu has been minimized
      .code-and-events-no-op {
        display: flex;
        background-color: var(--INTERACTIVE-BACKGROUND);
        max-height: 50vh;

        .code-panel {
          .cm-scroller {
            overflow: auto;
          }
        }

        // Code panel class when events panel has been minimized
        .code-panel-no-events {
          width: 100vw;

          .cm-scroller {
            overflow: auto;
          }
        }

        .events-panel {
          flex: 1;
          display: flex;
          max-width: calc(25vw - 5px);
        }
      }
    }
  }
  .content.dark {
    .operations-menu {
      background-color: var(--DM-PRIMARY-BACKGROUND);
    }
    .non-operations-menu {
      .code-panel-and-events-panel {
        .code-panel {
          .size-button__max {
            .code-max {
              color: var(--DM-SECONDARY-TEXT);
            }
          }
        }
        .code-panel-no-events {
          .size-button__min {
            .code-min {
              color: var(--DM-SECONDARY-TEXT);
            }
          }
        }
      }
      .code-and-events-no-op {
        .code-panel {
          border-right: 2px solid var(--DM-BORDER);
          .size-button__max {
            .code-max {
              color: var(--DM-SECONDARY-TEXT);
            }
          }
        }
        .code-panel-no-events {
          .size-button__min {
            .code-min {
              color: var(--DM-SECONDARY-TEXT);
            }
          }
        }
      }
    }
  }
}

// The CSS for card style header.
// * Fixed height 40px
// * No bottom margin
// * rounded corner on top
.card-header {
  padding: 5px;
  border-radius: 10px 10px 0px 0px;
  background-color: var(--CARD-HEADER-COLOR);
  height: 40px;
}

.card-shadow {
  border-radius: 10px 10px 0px 0px;
  box-shadow: 0 0 8px 0 rgb(0 0 0 / 38%);
  margin: 15px 10px 10px 10px;
}

.dark.app {
  //Fixing scroll bar color. Ref: https://developer.mozilla.org/en-US/docs/Web/CSS/color-scheme
  color-scheme: dark;
}

// Dark mode for all
.dark {
  background-color: var(--DM-PRIMARY-BACKGROUND);
  border-color: var(--DM-BORDER);
  color: var(--DM-SECONDARY-TEXT);

  .card-header {
    background-color: var(--DM-CARD-HEADER-COLOR);
  }

  .card-shadow {
    box-shadow: 0px 0px 6px lightgray;
  }

  button {
    color: var(--DM-SECONDARY-TEXT);
  }

  .__react_component_tooltip.place-top::after {
    border-top-color: var(--DM-SECONDARY-BACKGROUND);
    border-top-style: solid;
    border-top-width: 8px;
  }
  .__react_component_tooltip.place-bottom::after {
    border-bottom-color: var(--DM-SECONDARY-BACKGROUND);
    border-bottom-style: solid;
    border-bottom-width: 8px;
  }
  .__react_component_tooltip.place-left::after {
    border-left-color: var(--DM-SECONDARY-BACKGROUND);
    border-left-style: solid;
    border-left-width: 8px;
  }
  .__react_component_tooltip.place-right::after {
    border-right-color: var(--DM-SECONDARY-BACKGROUND);
    border-right-style: solid;
    border-right-width: 8px;
  }
  .react-tooltip {
    background-color: var(--DM-SECONDARY-BACKGROUND);
    box-shadow: 0 2px 6px var(--LIGHT-GRAY);
  }
  .tooltip-text {
    color: var(--DM-PRIMARY-TEXT);
  }
  button {
    color: var(--DM-SECONDARY-TEXT);
    &:hover {
      color: var(--DM-PRIMARY-TEXT);
    }
  }
}
