@import '../../variables.scss';
.dossier-selection:focus {
  outline: none;
}
.dossier-selection {
  position: absolute;
  width: 360px;
  height: 625px;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  align-items: center;
  margin: auto;
  background-color: var(--INTERACTIVE-BACKGROUND);
  border-radius: 4px;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  z-index: 1000; /* z-index need to be higher than rest of app to keep this popup over them*/
  &-container {
    &__title {
      text-align: center;

      &__content {
        margin-top: 20px;
        font-family: 'Roboto 500', sans-serif;
        font-size: 20px;
        color: var(--FONT-COLOR);
        margin-bottom: 30px;
      }

      .tab {
        text-align: center;
        margin: 0px 20px 0px 20px;
        font-family: 'Roboto 500', sans-serif;
        font-size: 16px;
        color: var(--SECONDARY-TEXT);
        font-weight: 500;
        display: inline-block;
        cursor: pointer;
        &:hover {
          color: var(--FONT-COLOR);
        }
      }

      .selected {
        text-decoration: underline;
        color: var(--FONT-COLOR);
        text-decoration-color: var(--PRIMARY-BLUE);
        text-decoration-thickness: 3px;
        text-underline-offset: 6px;
      }
    }
    &__button {
      width: 250px;
      padding: 10px;
      border: none;
      border-radius: 3px;
      background-color: var(--PRIMARY-BLUE);
      color: var(--color-white);
      font-size: 14px;
      font-weight: bold;
      cursor: pointer;
      pointer-events: auto;
      &:hover {
        background-color: var(--BUTTON-PRIMARY-HOVER);
      }
      &--disabled {
        cursor: default;
        pointer-events: none;
        background-color: var(--BUTTON-PRIMARY-INACTIVE);
      }
    }

    &__content {
      margin-top: 40px;
      height: 100%;
      width: 100%;
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: space-evenly;

      &__form {
        position: relative;
        width: 250px;

        .url-input {
          width: 100%;
          align-self: center;
          padding-top: 5px;
          padding-bottom: 8px;
          background-color: inherit;
          font-size: 16px;
          border: none;
          border-bottom: 2px solid var(--DARK-GRAY);
          margin-top: 28px;
        }
        .url-input:focus {
          outline: none;
          border: none;
          border-bottom: 2px solid var(--PRIMARY-BLUE);
        }
        .dossier-selection-dropdown-label {
          position: relative;
          display: block;
          margin-bottom: 5px;
          color: var(--SECONDARY-TEXT);
        }
        .dossier-selection-dropdown {
          &__content {
            align-self: center;
            width: 100%;
            border: 2px solid #cfd1d3;
            padding: 3px 3px 3px 3px;
            cursor: pointer;
            user-select: none;
            margin-bottom: 0px;
            border-radius: 5px;

            &:focus {
              border-color: var(--PRIMARY-BLUE);
            }
            &--selected {
              display: inline-block;
              overflow-x: hidden;
              text-overflow: ellipsis;
              width: 200px;
              white-space: nowrap;
              height: 24px;
              padding-top: 5px;
              margin-left: 10px;
              font-size: 16px;
              font-family: 'Roboto';
            }
          }
          &__container {
            align-self: center;
            width: 100%;
            margin-bottom: 140px;
          }
          .dossier-selection-dropdown-arrow {
            float: right;
            font-family: 'Material Icons';
            padding: 5px;
            font-weight: 800;
            transition: 0.5s transform ease;
            transform: rotate(90deg);
          }
          .dossier-selection-dropdown-arrow:before {
            content: '\e5e1';
          }

          .dossier-selection-dropdown-group {
            background: var(--INTERACTIVE-BACKGROUND);
            position: absolute;
            display: block;
            transition: all 0.3s linear;
            list-style-type: none;
            border-radius: 5px;
            border: thin solid transparent;
            border-top: none;
            width: 100%;
            height: 0;
            overflow-y: auto;
            overflow-x: hidden;
            padding-top: 5px;
            visibility: hidden;

            &__group {
              list-style-type: none;
              border-top: none;
              width: 100%;
              &__header {
                height: 20px;
                font-size: 12px;
                font-family: 'Roboto Bold';
                letter-spacing: 0.5px;
                line-height: 20px;
                padding-left: 15px;
                padding-top: 1px;
              }
              &__option {
                height: 28px;
                cursor: pointer;
                font-size: 16px;
                line-height: 28px;
                padding-left: 30px;
                &:hover {
                  background-color: var(--BUTTON-SECONDARY-PRESSED);
                }
              }
              &__option--selected {
                height: 28px;
                cursor: pointer;
                font-size: 16px;
                line-height: 28px;
                padding-left: 30px;
                &:hover {
                  background-color: var(--BUTTON-SECONDARY-PRESSED);
                }
              }
            }
          }
          .open {
            &.dossier-selection-dropdown__content {
              border-color: var(--PRIMARY-BLUE);
            }
            &.dossier-selection-dropdown-arrow {
              transform: rotate(270deg);
              transition: 0.5s transform ease;
            }
            &.dossier-selection-dropdown-group {
              border: thin solid var(--DARK-GRAY);
              border-top: none;
              height: 240px;
              visibility: visible;
            }
          }
        }
      }

      .placeholder {
        position: absolute;
        color: var(--SECONDARY-TEXT);
        font-size: 16px;
        top: 32px;
        transition: all 0.3s;
        z-index: -1;
        &.active {
          font-size: 13px;
          top: 14px;
          transition: all 0.3s;
        }
      }

      .error-message {
        height: 47px;
        margin-top: 8px;
        margin-bottom: 86px;
        align-items: left;
        text-align: left;

        &__closed {
          display: none;
        }

        &__open {
          color: var(--WARNING-RED);
          font-size: 12px;
          display: block;
        }
      }
    }
  }
}

.dossier-selection.dark {
  background-color: var(--DM-PRIMARY-BACKGROUND);
  .dossier-selection-container {
    &__title {
      &__content {
        color: var(--DM-PRIMARY-TEXT);
      }
      div {
        color: var(--DM-SECONDARY-TEXT);
      }
      .tab {
        color: var(--DM-SECONDARY-TEXT);
        &:hover {
          color: var(--DM-PRIMARY-TEXT);
        }
      }
      .selected {
        color: var(--DM-PRIMARY-TEXT);
      }
    }
    &__content {
      &__form {
        color: var(--DM-SECONDARY-TEXT);
        .url-input {
          color: var(--DM-PRIMARY-TEXT);
          border-bottom: 2px solid var(--DM-SECONDARY-TEXT);
        }
        .url-input:focus {
          border-bottom: 2px solid var(--PRIMARY-BLUE);
        }
        .dossier-selection-dropdown-label {
          color: var(--DM-SECONDARY-TEXT);
        }
        .dossier-selection-dropdown-group {
          background-color: var(--DM-PRIMARY-BACKGROUND);
          &__group__option:hover {
            background-color: var(--DM-SECONDARY-BACKGROUND);
          }
          &__group__option--selected:hover {
            background-color: var(--DM-SECONDARY-BACKGROUND);
          }
        }
        .placeholder {
          color: var(--DM-SECONDARY-TEXT);
        }
      }
    }
  }
}

.dossier-selection-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(#000000, 0.6);
  z-index: 1000; /* z-index need to be higher than rest of app to keep overlay over them*/
}
