@import '../../variables.scss';

.navigation-bar {
  position: relative;
  box-shadow: 0px 0px 6px 2px var(--SHADOW-COLOR);
  overflow: visible;
  z-index: 10;
}

.navigation-bar.dark {
  box-shadow: 0px 0px 6px 1px var(--DM-SHADOW-COLOR);
}

.menu {
  height: var(--navigation-bar-height);
  min-height: var(--navigation-bar-height);
  display: flex;
  align-items: center;
  flex-direction: row;
  font-size: 14px;
  font-family: 'Roboto 500', sans-serif;

  .logo {
    margin-left: 5px;
    img {
      height: calc(var(--navigation-bar-height) - 15px);
      margin: 2px 0px 0px 15px;
    }
  }
  .title {
    flex-grow: 1;
    flex-shrink: 1;
    font-size: 1.25em;
    color: var(--PRIMARY-TEXT);
    margin: 2px 10px 0px 10px;
    border-left: 1px solid var(--PRIMARY-TEXT);
    padding: 5px 10px 5px 10px;
  }

  .toggle {
    color: var(--PRIMARY-TEXT);
  }
  .switch {
    position: relative;
    display: inline-block;
    width: 44px;
    height: 26px;
    margin-left: 5px;
    margin-right: 15px;

    /* Hide default HTML checkbox */
    & input {
      opacity: 0;
      width: 0;
      height: 0;
    }
  }
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--PRIMARY-TEXT);
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }

  .slider:before {
    position: absolute;
    content: '🌙';
    height: 22px;
    width: 22px;
    left: 4px;

    -webkit-transition: 0.4s;
    transition: 0.4s;
    padding: 2px;
    font-size: 14px;
    line-height: 22px;
  }

  .slider:hover {
    background-color: var(--PRIMARY-BLUE);
  }

  input:checked + .slider:before {
    -webkit-transform: translateX(15px);
    -ms-transform: translateX(15px);
    transform: translateX(15px);
  }

  /* Rounded sliders */
  .slider.round {
    border-radius: 26px;
  }

  .slider.round:before {
    border-radius: 50%;
  }

  button {
    margin-right: 20px;
  }
  .button__start-over {
    display: flex;
    align-items: center;
    border-radius: 5px;
    border: 1px solid var(--PRIMARY-BLUE);
    margin-right: 10px;
    cursor: pointer;
    font-size: 14px;
    font-family: 'Roboto 500', sans-serif;
    color: var(--color-white);
    background-color: var(--PRIMARY-BLUE);
    padding: 2px 5px 2px 2px;
    &:hover {
      color: var(--color-white);
      background-color: var(--BUTTON-PRIMARY-HOVER);
      border-color: var(--BUTTON-PRIMARY-HOVER);
    }

    p {
      margin-left: 2px;
    }
  }
}

.menu.dark {
  background-color: var(--DM-PRIMARY-BACKGROUND);
  .toggle,
  .title {
    color: var(--DM-PRIMARY-TEXT);
  }

  .title {
    border-left: 1px solid var(--DM-PRIMARY-TEXT);
  }

  .slider {
    background-color: var(--color-white);
  }

  .slider:before {
    content: '☀️';
  }

  .slider:hover {
    background-color: var(--PRIMARY-BLUE);
  }
}

.button-logout {
  margin-right: 15px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 5px;
  &:hover {
    color: var(--color-red);
  }
}
