@import '../../variables.scss';

.code-mirror {
  .panel-header {
    display: flex;
    position: relative;
    align-items: center;
    flex-direction: row;
    .title {
      flex: 1;
      font-family: 'Roboto 500', sans-serif;
      margin-left: 10px;
      font-size: 16px;
      color: var(--FONT-COLOR);
    }
    .buttons {
      display: flex;

      .button__run {
        display: flex;
        align-items: center;
        border-radius: 5px;
        border: 1px solid var(--PRIMARY-BLUE);
        margin-right: 10px;
        cursor: pointer;
        font-size: 14px;
        font-family: 'Roboto 500', sans-serif;
        color: var(--color-white);
        background-color: var(--PRIMARY-BLUE);
        padding: 2px 5px 2px 0;
        margin: 5px 10px 5px 0;
        &:hover {
          color: var(--color-white);
          background-color: var(--BUTTON-PRIMARY-HOVER);
          border-color: var(--BUTTON-PRIMARY-HOVER);
        }
      }
    }
  }

  .code-mirror-container {
    border-top: 1px solid var(--BORDER);
    background-color: var(--color-white);
  }

  .code-mirror-container.dragging {
    .cm-line.cm-activeLine {
      // use box-shadow to avoid changing selection div height
      box-shadow: 0 1px 0 var(--color-red) inset;
    }
  }

  .cm-editor.cm-focused {
    outline: none;
  }

  .cm-content,
  .cm-gutter {
    min-height: 450px;
  }
}

//Special dark mode settings.
//Background color is set by global dark style
.code-mirror.dark {
  .title {
    color: var(--DM-PRIMARY-TEXT);
  }

  .code-mirror-container {
    border-color: var(--DM-BORDER);
  }
}
