@import '../../variables.scss';

.operations-menu {
  max-height: calc(100vh - 10px);
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 1;
  border-right: 2px solid var(--BORDER);
  margin-right: 5px;
  margin-left: 0px;
  //Menu title
  .head {
    font-weight: 500;
    font-weight: bold;
    font-size: 18px;
    font-family: 'Roboto-Medium', sans-serif;
    position: relative;
    text-align: left;
    align-items: center;
    margin-left: var(--margin-left-spacing);
    margin-top: 20px;
    margin-bottom: 20px;
    flex-grow: 0;
  }

  /*Search Bar*/
  .search-bar {
    height: 32px;
    padding: 5px;
    position: relative;
    margin-left: var(--margin-left-spacing);
    margin-right: var(--margin-right-spacing);
    margin-bottom: 15px;
    z-index: -1;

    border: none;

    input:focus {
      border: none;
      outline: none;
    }
    .search-input::placeholder {
      color: var(--SECONDARY-TEXT);
    }
    .search-input {
      width: 100%;
      height: 32px;
      position: absolute;
      background-color: var(--LIGHT-GRAY);
      top: 0;
      left: 0;
      z-index: -1;
      border-radius: 5px;
      border: none;
      color: var(--PRIMARY-TEXT);
      font-size: 14px;
      padding: 5px 5px 5px 28px;
    }
  }

  .details-wrapper {
    height: 100%;
    flex: 1;
    overflow-y: auto;
  }

  .details-wrapper::-webkit-scrollbar {
    width: 7px;
  }

  .details-wrapper::-webkit-scrollbar-thumb {
    background-color: var(--scrollbar-color);
    border-radius: 5px;
  }

  //Contains all drop downs and summaries
  .operation-item-list {
    text-align: left;
    display: flex;
    position: relative;
    flex-direction: column;
    background: transparent;
    margin-bottom: 0px;
    //Element within each list
    //Conntains drag and drop, and tooltip
    .operation-item:nth-child(1) {
      margin-top: 5px;
    }
    // // We set it to no display to prevent draggable bug
    // .__react_component_tooltip {
    //     display: none;
    // }
    .operation-item {
      display: flex;
      align-items: center;
      cursor: grab;
      list-style: none;
      list-style-position: inside;
      padding-top: 5px;
      padding-bottom: 5px;
      text-align: left;
      font-size: 15px;
      position: relative;
      display: flex;
      font-weight: 400;
      line-height: 1.5;
      vertical-align: middle;
      padding-left: var(--margin-left-spacing);
      padding-right: var(--margin-right-spacing);
      background-color: transparent;

      &:hover {
        background-color: var(--INTERACTIVE-BACKGROUND);
      }
      &:hover > .info {
        visibility: visible;
        align-items: center;
        justify-content: left;
        position: relative;
        .information-fill {
          text-decoration: none;
          font-style: normal;
          color: var(--FONT-COLOR);

          &:hover {
            color: var(--PRIMARY-BLUE);
          }

          &:before {
            font-family: 'Material Icons Outlined';
            content: 'info';
            font-size: 20px;
          }
        }
      }

      //Drag and drop operation item
      .item {
        display: inline-flex;
        width: 100%;
        padding-left: 12px;
        padding-right: 9px;
        padding-top: 3px;
        .item__text {
          color: var(--PRIMARY-TEXT);
          font-size: 14px;
          line-height: 16px;
          font-family: 'Roboto 500', sans-serif;
        }
      }

      //Info sign for tool tip hidden
      .info {
        cursor: auto;
        visibility: hidden;
        .information-fill {
          text-decoration: none;
          font-style: normal;
          color: var(--FONT-COLOR);

          &:hover {
            color: var(--PRIMARY-BLUE);
          }

          &:before {
            font-family: 'Material Icons Outlined';
            content: 'info';
            font-size: 20px;
          }
        }
      }
      // // We set it to display the tooltip when it is not possible to drag
      // .information-fill:hover + .__react_component_tooltip {
      //     display: block;
      // }

      .tooltip {
        display: flex;
        flex-direction: column;
      }

      .tooltip-text {
        display: table;
        table-layout: fixed;
        max-width: 200px;
        text-align: left;
        vertical-align: center;
        background-color: var(--color-white);
        color: var(--color-black);
        font-family: 'Roboto', sans-serif;
      }

      .tooltip-link {
        margin-top: 10px;
        font-family: 'Roboto 500', sans-serif;
        color: var(--PRIMARY-BLUE);
        background-color: var(--color-white);
        a {
          color: var(--PRIMARY-BLUE);
        }
      }
    }
    .operation-item:active {
      cursor: grabbing;
      background-color: transparent;
      .info {
        visibility: hidden;
      }
    }
  }
  @keyframes details-show {
    from {
      margin-bottom: -20%;
      opacity: 0;
      transform: translateY(-100%);
    }
  }
  //Entire dropdown menu; contains summary and operations
  details {
    box-sizing: border-box;
    text-align: left;
    display: flex;
    flex-direction: column;
    background-color: transparent;
    overflow: hidden;
    margin-bottom: 20px;
    transition: all 0.3s linear;
    &:hover::before {
      color: var(--color-red);
    }
    &[open] > summary:after {
      transform: rotate(270deg);
      transition: 0.3s transform ease;
    }
    &[open] > .operation-item-list {
      animation: details-show 300ms ease-in-out;
    }
  }

  //Drop down menu heading
  summary {
    height: var(--summary-height);
    font-family: 'Roboto-Medium', sans-serif;
    outline: none;
    display: block;
    font-weight: bold;
    background-color: var(--color-white);
    color: var(--color-black);
    padding-left: var(--margin-left-spacing);
    padding-right: var(--margin-right-spacing);
    position: relative;
    cursor: pointer;
    font-size: 17px;
    z-index: 0;

    &:hover {
      color: var(--color-red);
      z-index: 1;
    }
    &::after {
      font-family: 'Material Icons';
      font-size: 20px;
      content: '\e5e1';
      transform: rotate(90deg);
      float: right;
      z-index: -1;
      transform-origin: center;
      transition: 0.3s transform ease;
      background-color: inherit;
    }
  }

  //Documentation link
  .link {
    border-top: 2px solid var(--BORDER);
    list-style: none;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: var(--margin-left-spacing);
    text-align: left;
    font-size: 14px;
    flex: 0;
    .link-text {
      color: var(--PRIMARY-BLUE);
    }
  }

  a {
    text-decoration: none;
  }
}

.operations-menu.dark {
  .search-bar {
    .search-input {
      color: var(--DM-SECONDARY-TEXT);
      background-color: var(--DM-SECONDARY-BACKGROUND);
    }
    .search-icon::before {
      color: var(--DM-SECONDARY-TEXT);
    }

    input:focus {
      border: none;
    }
  }

  li {
    background-color: inherit;
    color: var(--DM-PRIMARY-TEXT);
  }

  body {
    background-color: var(--DM-PRIMARY-BACKGROUND);
  }

  details {
    background-color: inherit;
  }

  summary {
    background-color: inherit;
    color: var(--DM-SECONDARY-TEXT);
    &:hover {
      color: var(--color-red);
    }
  }

  .operation-item-list {
    .operation-item {
      &:hover {
        background-color: var(--DM-INTERACTIVE-BACKGROUND);
      }
      .item {
        padding-top: 3px;
        .item__text {
          color: var(--DM-SECONDARY-TEXT);
          line-height: 16px;
          font-size: 14px;
          font-family: 'Roboto 500', sans-serif;
        }
      }
      .info {
        .react-tooltip {
          .tooltip-text {
            background-color: inherit;
            color: var(--DM-PRIMARY-TEXT);
          }
          .tooltip-link {
            background-color: inherit;
          }
        }
        .information-fill {
          color: var(--DM-PRIMARY-TEXT);
          &:hover {
            color: var(--PRIMARY-BLUE);
          }
        }
      }
    }
    .operation-item:active {
      cursor: grabbing;
      background-color: transparent;
      .info {
        visibility: hidden;
      }
    }
  }

  .link {
    border-top: 2px solid var(--DM-BORDER);
  }
}
