@import '../../variables.scss';

.events-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-width: 0;
  min-height: 142px;
  .event-container__header {
    display: flex;
    position: relative;
    align-items: center;
    flex-direction: row;
    flex-shrink: 0;

    .tabs {
      display: flex;
      flex: 1;
      flex-direction: row;
      column-gap: 40px;
      margin-right: 20px;
      margin-left: 10px;
      .tab {
        font-family: 'Roboto 500', sans-serif;
        font-size: 16px;
        color: var(--SECONDARY-TEXT);
        cursor: pointer;
        &:hover {
          color: var(--FONT-COLOR);
        }
      }
      .tab-selected {
        font-family: 'Roboto 500', sans-serif;
        font-size: 16px;
        color: var(--FONT-COLOR);
        cursor: pointer;
        text-underline-offset: 10px;
        // text-decoration: 3px underline var(--PRIMARY-BLUE); // doesn't work for safari.
        text-decoration-line: underline;
        text-decoration-thickness: 3px;
        text-decoration-color: var(--PRIMARY-BLUE);
      }
      .red-circle {
        position: absolute;
        height: 10px;
        width: 10px;
        border-radius: 50%;
        background-color: var(--color-red);
        top: 10px;
      }
      .hide {
        display: none;
      }
    }
    .clear_events {
      flex: 0;
      position: absolute;
      right: 0;
      font-family: 'Roboto 500', sans-serif;
      font-size: 14px;
      border: none;
      cursor: pointer;
      background-color: inherit;
      padding: 5px 10px;

      color: var(--SECONDARY-TEXT);
      &:hover {
        color: var(--FONT-COLOR);
      }
    }
  }

  .event-container__text {
    flex: 1;
    border-top: 1px solid var(--BORDER);
    font-size: 12px;
    font-family: 'Roboto', sans-serif;
    overflow: auto;
    background-color: var(--color-white);
    font-weight: 400;

    pre {
      margin: 10px;
    }
  }
}
.events-container.dark {
  //Use the same background color as CM
  // background-color: var(--DM-EDITOR_BACKGROUND);
  .event-container__header {
    .tab-selected {
      color: var(--DM-PRIMARY-TEXT);
    }
    .tab {
      color: var(--DM-SECONDARY-TEXT);
      &:hover {
        color: var(--DM-PRIMARY-TEXT);
      }
    }
    .clear_events {
      color: var(--DM-SECONDARY-TEXT);
      &:hover {
        color: var(--DM-PRIMARY-TEXT);
      }
      .clear {
      }
    }
    .red-circle {
      top: 10.5px;
    }
  }
  .event-container__text {
    color: var(--DM-PRIMARY-TEXT);
    background-color: var(--DM-PRIMARY-BACKGROUND);
    border-color: var(--DM-BORDER);
  }
}
